.dashboardContainer h1{
    margin-bottom: 5vh;
    font-size: 27px;
}

.resumeContainer{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    margin-bottom: 4vh;
}

/* USED IN DASHBOARDCARD COMPONENT (START)*/
.resumeBox{
    border-radius: 0 5px 5px 0;
    background-color: white;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    display: flex;
}
.resumeBox svg{
    font-size: 75px;
    padding: 10px;
    color: white !important;
}
.boxOne,
.boxTwo,
.boxThree,
.boxFour{
    width: 30%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.boxOne{
    background-color: #16A2B9;
}
.boxTwo{
    background-color: #26A844;
}
.boxThree{
    background-color: #FEC009;
}
.boxFour{
    background-color: #DC3546;
}
.resumeContainer .resumeBox:first-child{
    border-bottom: 5px solid #1891A4;
}
.resumeContainer .resumeBox:nth-child(2){
    border-bottom: 5px solid #279840;
}
.resumeContainer .resumeBox:nth-child(3){
    border-bottom: 5px solid #E6AC0C;
}
.resumeContainer .resumeBox:last-child{
    border-bottom: 5px solid #C5303E;
}
.data{
    width: 70%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.data p{
    font-weight: 700;
    font-size: 17px;
}
.data span{
    font-size: 20px;
    margin-top: 5px;
    cursor: pointer;
    color: #16A2B9;
    text-decoration: underline;
    transition: all 300ms;
}
span.noLink{
    text-decoration: none;
    cursor: initial;
}
.data span:hover{
    color: #10cce9;
}
span.noLink:hover{
    color: #16A2B9;
}
/* USED IN DASHBOARDCARD COMPONENT (END)*/

.tableContainer{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    margin-bottom: 4vh;
}

.tableContainer .infoTable{
    min-height: 500px;
    display: flex;
    flex-direction: column;
}

.infoTable{
    background-color: white;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;

}

.infoTable .orders{
    width: 100%;
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.orders span{
    font-size: 25px;
    font-weight: bold;
}

.infoTable h2{
    font-family: 'Nunito Sans', sans-serif;
    font-weight: 600;
}

.topProducts{
    width: 100%;
    background-color: white;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}

.tableHeader{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1em;
}

.tableHeader button{
    background-color: #FE6849;
    
}
.tableHeader button a{
    color: whitesmoke
}

.tableHeader button:hover{
    background-color: #fa7c63;
}

hr{
    border-bottom: 1px solid #444;
}



.bestSeller{
    margin-top: 1em;
    padding: 0 1em;
    padding-bottom: 3vh;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: .8em;
    margin-bottom: 4vh;
}

.tableContainerBig{
    display: grid;
    grid-template-columns: 3fr 2fr 2fr;
    gap: 20px;
    margin-bottom: 4vh;
    border-radius: 5px;
}
