.mainContainer{
    width: 100%;
    min-height: 100vh;
    display: flex;
}

.bodyContainer{
    display: flex;
    flex-direction: column;
    flex: 1
}
section.header{
    width: 100%;
    height: 11vh;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 3vw;
    padding-left: 1vw;
}

.mainContainer *{
    font-family: 'Nunito Sans', sans-serif;
}

.mainContainer h1, h2, p, span{
    color: #222;
}

section.header svg{
    font-size: 35px;
    color: #cc533a;
    cursor: pointer;
    transition: all 300ms;
}

section.header i:hover{
    color: #fe6849;
}

.userSection{
    display: flex;
    align-items: center;
}

.userSection svg{
    margin-right: 1em;
    font-size: 27px !important;
}

.profilePic{
    width: 60px;
    height: 60px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    cursor: pointer;
}


.viewContainer{
    background-color: #F0F0FA;
    flex: 1;
    padding: 5vh 2vw;
}

